<template>
  <div class="login">
    <navgation-bar @onLeftClick="onBackClick" :pageName="'登录'"></navgation-bar>
    <!-- 内容区 -->
    <div class="login-content">
      <!-- 用户名 -->
      <div class="input-container">
        <input type="text" v-model="username" placeholder="用户名" />
      </div>
      <!-- 密码 -->
      <div class="input-container">
        <input type="password" v-model="password" placeholder="密码" />
      </div>
      <div class="login btn page-commit" @click="onLoginClick">登录</div>
      <a
        @click="onToRegisterClick"
        href="javascript:;"
        class="login-page-content-register btn"
      >注册新用户</a>
    </div>
  </div>
</template>
<script>
import NavgationBar from '../components/currency/NavgationBar.vue'
export default {
  components: {
    NavgationBar
  },
  data() {
    return {
      // 用户名
      username: '',
      //   密码
      password: ''
    }
  },
  methods: {
    onBackClick() {
      this.$router.go(-1)
    },
    // 登录按钮点击事件
    onLoginClick() {
      // 验证用户名和密码【表情】
      if (this.username.length === 0 || this.password.length === 0) {
        alert('请填写完整的数据')
        return ''
      }
    },
    // 点击按钮跳转到注册页面
    onToRegisterClick() {
      this.$router.push({
        name: 'register',
        params: {
          routerType: 'push'
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.login {
  margin: 80/20rem 0;

  height: 100%;
  width: 100%;
  background-color: #fff;
  .login-content {
    width: 100%;
    height: 100%;
    .login-page-content-register {
      display: block;
    }
  }
}
</style>
